<template>
  <div class="login-container">
    <el-form class="login-form" autoComplete="on" label-position="left">

      <div class="title-container">
        <h3 class="title">彼益网络 - 后台管理</h3>
      </div>

      <el-form-item>
        <span class="svg-container svg-container_login">
          <svg-icon icon-class="user" />
        </span>
        <el-input type="text" v-model="username" autoComplete="on" placeholder="username..." />
      </el-form-item>

      <el-form-item>
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input :type="passwordType" @keyup.enter.native="handleLogin" v-model="password" autoComplete="on" placeholder="password..." />
        <span class="show-pwd" @click="showPwd">
          <svg-icon icon-class="eye" />
        </span>
      </el-form-item>

      <el-button type="primary" class="submit-login" :loading="loading" @click.native.prevent="handleLogin">登录</el-button>

      <div class="cached-login">
        <el-checkbox v-model="checked">一周内免登录</el-checkbox>
      </div>

    </el-form>

  </div>
</template>

<script>
import Cookies from 'js-cookie'
import { fetchData } from '@/api'
export default {
  name: 'login',
  data() {
    return {
      username: Cookies.get('username') ? Cookies.get('username') : '',
      password: Cookies.get('password') ? Cookies.get('password') : '',
      checked: false,
      passwordType: 'password',
      loading: false
    }
  },
  methods: {
    // 显示密码
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
    },
    // 登录
    handleLogin() {
      if (!this.username || !this.password) {
        ELEMENT.Message.error('请输入用户名或密码')
        return false
      }
      this.loading = true
      fetchData('userLogin', {
        username: this.username,
        password: this.password,
        remember: this.checked ? 1 : 0
      }).then(data => {
        if (!data) {
          this.loading = false
          return false
        }
        Cookies.set('username', this.username, { expires: 365 })
        Cookies.set('password', this.password, { expires: 365 })
        this.$store.dispatch('fetchUserInfo').then(() => {
          window.location.href = `${window.location.origin}/`
        })
      })
    }
  }
}
</script>

<style lang="less">
/* reset element-ui css */

.login-container {
  .el-form-item__content {
    line-height: 30px;
  }
  .el-input {
    display: inline-block;
    width: 85%;
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 5px 10px;
      height: 42px;
      line-height: 40px;
      color: #eee;
      &:-webkit-autofill {
        -webkit-box-shadow: 0 0 0px 1000px #2d3a4b inset !important;
        -webkit-text-fill-color: #fff !important;
      }
    }
  }
  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}

.login-container {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: #2d3a4b;
  .login-form {
    position: absolute;
    left: 0;
    right: 0;
    width: 420px;
    padding: 35px 35px 15px 35px;
    margin: 120px auto;
    @media screen and (max-width: 420px) {
      width: 260px;
      padding: 0;
    }
  }
  .svg-container {
    padding: 6px 5px 6px 15px;
    color: #889aa4;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
    &_login {
      font-size: 20px;
    }
    svg {
      vertical-align: text-top;
    }
  }
  .title-container {
    position: relative;
    .title {
      font-size: 18px;
      padding-bottom: 30px;
      font-weight: 400;
      color: #eee;
      margin: 0px auto;
      text-align: center;
      font-weight: bold;
    }
  }
  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: #889aa4;
    cursor: pointer;
    user-select: none;
  }
  .submit-login {
    width: 100%;
    margin-bottom: 30px;
  }
  .cached-login {
    text-align: right;
    .el-checkbox {
      color: #fff;
    }
  }
}
</style>




